<template>
  <div class="comment">
    <div class="comment-user">
      <div class="user-avatar">
        <img src="../assets/logo.png">
      </div>
    </div>
    <div class="comment-content">
      <div class="user-name">
      {{ comment.username }}
      </div>
      {{ comment.content }}
    </div>
    <div class="like" :class="{liked: isLiked}"  @click="likeHandle">
      <i class="iconfont">{{ isLiked ? '&#xe793;' : '&#xe794;'}} </i>
      <span class="count">{{ comment.likecount}}</span>
    </div>
  </div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
export default {
  name: 'comment',
  props: ['comment'],
  data () {
    return {
      isLiked: false
    }
  },
  methods: {
    likeHandle () {
      this.isLiked = true
      Indicator.open()
      this.axios.post('http://60.205.203.185:8082/rest/topicback/like/' + this.$ls.get('token') + '/' + this.comment.id)
      .then(({data}) => {
        Indicator.close()
        if (data.msg === 'OK') {
          this.comment.likecount += 1
        } else {
          Toast({
            message: data.msg,
            position: 'bottom',
            duration: 2000
          })
        }
      })
      .catch(data => {
        Indicator.close()
        Toast({
          message: '网络连接错误',
          position: 'bottom',
          duration: 2000
        })
      })
    }
  }
}
</script>

<style lang="sass" scoped>
.comment
  padding: .5em 1em
  .comment-user
    display: inline-block
    vertical-align: top
    .user-avatar
      img
        width: 2em
        height: 2em
        border: 1px solid #ccc
        border-radius: 1em
  .comment-content
    display: inline-block
    .user-name
      font-size: .8em
  .like
    float: right
    color: #c5c5c5
    &.liked
      color: #ff1313
    .iconfont
      font-size: 1em
      vertical-align: middle
    .count
      font-size: .6em
</style>



