<template>
  <div class="funs-list">
    <mt-loadmore :top-method="loadTop" ref="loadmore">
      <FunItem v-for="(fun, i) in funs" :fun="fun" :key='i'></FunItem>
    </mt-loadmore>
  </div>
</template>

<script>
import FunItem from './FunItem.vue'
import { Toast, Indicator } from 'mint-ui'

export default {
  name: 'FunsList',
  data () {
    return {
      funs: []
    }
  },
  created () {
    this.loadData(Indicator.close)
  },
  methods: {
    loadTop () {
      this.$ls.remove('funs')
      this.loadData(this.$refs.loadmore.onTopLoaded)
    },
    loadData (cb) {
      let funs = this.$ls.get('funs')
      if (funs === null) {
        if (cb === Indicator.close) {
          Indicator.open()
        }
        this.axios.get('http://60.205.203.185:8082/rest/topic/list/1/10')
        .then(({data}) => {
          cb & cb()
          if (data.status === 200) {
            this.funs = data.data.rows
            this.$ls.set('funs', this.funs)
          }
          Toast({
            message: data.msg,
            position: 'bottom',
            duration: 2000
          })
        })
        .catch(() => {
          cb & cb()
          Toast({
            message: '网络连接错误',
            position: 'bottom',
            duration: 2000
          })
        })
      } else {
        this.funs = funs
      }
    }
  },
  components: {
    FunItem
  }
}
</script>

<style lang="sass" scoped>
.funs-list
  min-height: 300px
  .fun-item
    margin-bottom: .5em
</style>



