<template>
  <div class="fun">
    <mt-header fixed title="详 情">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <FunItem :fun="fun"></FunItem>
    <Comment v-for="(comment, i) in comments" key="i" :comment="comment"></Comment>
    <mt-spinner type="fading-circle" color="#26a2ff" v-show="isLoading"></mt-spinner>
    <div class="comment-input">
      <input @focus="toggleCommentInput" v-model="content" type="text" class="mint-field-core" placeholder="吐槽一下">
      <button v-show="inputFocus" @click="commentHandle" class="btn">发送</button>
    </div>
    <div v-show="inputFocus" @click="toggleCommentInput" class="mask"></div>
  </div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
import Comment from './comment'
import FunItem from './FunItem.vue'
export default {
  name: 'Fun',
  data () {
    return {
      comments: [],
      inputFocus: false,
      content: '',
      isLoading: true,
      fun: {}
    }
  },
  created () {
    this.isLoading = true
    this.axios.get('http://60.205.203.185:8082/rest/topic/info/' + this.$route.params.id)
    .then(({data}) => {
      this.fun = data.data
      this.axios.get('http://60.205.203.185:8082/rest/topicback/list/' + this.$route.params.id + '/1/20')
      .then(({data}) => {
        this.isLoading = false
        this.comments = data.data.rows
        console.log(this.comments)
        Toast({
          message: data.msg,
          position: 'bottom',
          duration: 2000
        })
      })
      .catch(({data}) => {
        this.isLoading = true
        Toast({
          message: '网络链接失败',
          position: 'bottom',
          duration: 2000
        })
      })
    })
    .catch(({data}) => {
      this.isLoading = true
      Toast({
        message: '网络链接失败',
        position: 'bottom',
        duration: 2000
      })
    })
  },
  methods: {
    toggleCommentInput () {
      this.inputFocus = !this.inputFocus
    },
    commentHandle () {
      let params = 'content=' + this.content + '&token=' + this.$ls.get('token') + '&fId=' + this.$route.params.id
      Indicator.open()
      this.axios.post('http://60.205.203.185:8082/rest/topicback/create', params)
      .then(({data}) => {
        Indicator.close()
        if (data.status === 200) {
          this.comments.push(data.data)
          this.content = ''
          this.inputFocus = false
        }
        Toast({
          message: data.msg,
          position: 'bottom',
          duration: 2000
        })
      })
      .catch(() => {
        Toast({
          message: '网络连接失败',
          position: 'bottom',
          duration: 2000
        })
      })
    }
  },
  components: {
    FunItem,
    Comment
  }
}
</script>

<style lang="sass">
.fun
  .mint-button-text
    vertical-align: middle
  .comment-input
    position: absolute
    bottom: 0
    width: 100%
    z-index: 10000
    background: #dcd9cf
    padding: .5rem
    box-sizing: border-box
    .mint-field-core
      border-radius: .25rem
      padding: .25rem .5rem
      box-sizing: border-box
    .btn
      border: none
      background: #b19685
      padding: .5rem 1rem
      margin-top: .5rem
      border-radius: .25rem
      color: #fff
      float: right
      outline: none
  .mask
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0
    background: rgba(0, 0, 0, 0.47)
    z-index: 1000
  .mint-spinner-fading-circle
    margin: 1rem auto
  .comment
    background: #fff
    border-top: 1px solid #e8e8e8
</style>



