<template>
  <div>
    <mt-header fixed title="投稿互动">
      <mt-button @click="backHandle" icon="back" slot="left"></mt-button>
    </mt-header>
    <Comment v-for="(comment, key) in comments" key='key' @click.native="redirectHandle(comment)" :comment="comment"></Comment>
  </div>
</template>

<script>
import Comment from '../comment'
import { Toast, Indicator } from 'mint-ui'
export default {
  name: 'comments',
  data () {
    return {
      comments: []
    }
  },
  created () {
    Indicator.open()
    this.axios.get('http://60.205.203.185:8082/rest/topicback/list/token/' + this.$ls.get('token'))
    .then(({data}) => {
      Indicator.close()
      if (data.status === 200) {
        this.comments = data.data
      }
      Toast({
        message: data.msg,
        position: 'bottom',
        duration: 2000
      })
    })
  },
  methods: {
    backHandle () {
      this.$router.back()
    },
    redirectHandle (comment) {
      this.$router.push({path: '/fun/' + comment.topicId})
    }
  },
  components: {
    Comment
  }
}
</script>

<style lang="sass" scoped>
  .comment
    background: #fff
    border-top: 1px solid #e8e8e8
</style>



