<template>
  <div class="fun-item" @click='toFunHandle'>
    <div class="fun-user">
      <div class="user-avatar">
        <img src="../assets/logo.png">
      </div>
      <div class="user-name">
        {{ fun.username}}
      </div>
    </div>
    <div class="fun-content">
      {{ fun.content }}
    </div>
    <div class="fun-god-comment" v-if="fun.hasGodComment">
      <comment :comment="fun.godComment"></comment>
    </div>
    <div class="fun-control">
      <div class="item" :class="{liked: isLiked}">
        <i class="iconfont" @click.prevent.stop="likeHandle" >{{ isLiked ? '&#xe793;' : '&#xe794;'}} </i>
        <span class="count">{{ fun.likecount }}</span>
      </div>
      <div class="item">
        <i class="iconfont">&#xe763;</i>
        <span class="count">{{ fun.replycount }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
import Comment from './comment'
export default {
  name: 'Fun',
  props: ['fun'],
  data () {
    return {
      isLiked: false
    }
  },
  methods: {
    toFunHandle () {
      this.$router.push({name: 'Fun', params: {id: this.fun.id}})
    },
    likeHandle () {
      this.isLiked = true
      Indicator.open()
      this.axios.post('http://60.205.203.185:8082/rest/topic/like/' + this.$ls.get('token') + '/' + this.fun.id)
      .then(({data}) => {
        Indicator.close()
        if (data.msg === 'OK') {
          this.fun.likecount += 1
        } else {
          Toast({
            message: data.msg,
            position: 'bottom',
            duration: 2000
          })
        }
      })
      .catch(data => {
        Indicator.close()
        Toast({
          message: '网络连接错误',
          position: 'bottom',
          duration: 2000
        })
      })
    }
  },
  components: {
    Comment
  }
}
</script>

<style lang="sass" scoped>
.fun-item
  padding: .5em .5em 0 .5em
  background: #fff
  .fun-user
    .user-avatar
      display: inline-block
      img
        width: 2em
        height: 2em
        border: 1px solid #ccc
        border-radius: 1em
        vertical-align: middle
    .user-name
      display: inline-block
      font-size: 0.8em
  .fun-god-comment .comment
    background: #f3f3f3
  .fun-control
    padding: .5em
    .item
      display: inline-block
      color: #c5c5c5
      margin-right: 1em
      &.liked
        color: #ff5a5a
      .fa
        vertical-align: middle
        font-size: 1.3em
        padding-right: .2em
      .count
        font-size: .7em
  .fun-content
    padding: .5rem
</style>



