<template>
  <div>
    <mt-header fixed title="我的投稿">
      <mt-button @click="backHandle" icon="back" slot="left"></mt-button>
    </mt-header>
      <div class="funs-list">
        <FunItem v-for="(fun, i) in data" :fun="fun" :key='i'></FunItem>
      </div>
  </div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
import FunItem from '../FunItem.vue'
export default {
  name: 'funs',
  props: ['user'],
  data () {
    return {
      data: []
    }
  },
  created () {
    console.log(this.user)
    Indicator.open()
    this.axios.get('http://60.205.203.185:8082/rest/topic/token/' + this.$ls.get('token'))
    .then(({data}) => {
      Indicator.close()
      if (data.status === 200) {
        data.data.forEach(function (v, i) {
          data.data[i].username = this.user.username
        }, this)
        this.data = data.data
      }
      Toast({
        message: data.msg,
        position: 'bottom',
        duration: 2000
      })
    })
    .catch(data => {
      Indicator.close()
      Toast({
        message: '网络连接错误',
        position: 'bottom',
        duration: 2000
      })
    })
  },
  methods: {
    backHandle () {
      this.$router.back()
    }
  },
  components: {
    FunItem
  }
}
</script>

<style lang="sass" scoped>
  .fun-item
    margin-bottom: .5em
</style>



